import React from 'react';
import {FaInstagram , FaLinkedinIn} from "react-icons/fa";
import logoforweb from "./../../images/logo/Logo-for-web_jan29-2.png";
import { Link } from "react-router-dom";


const SocialShare = [
    //{Social: <FaFacebookF /> , link: 'https://www.facebook.com/'},
    {Social: <FaLinkedinIn /> , link: 'https://www.linkedin.com/company/lucrorworld'},
    {Social: <FaInstagram /> , link: 'https://www.instagram.com/lucrorworld'},
   // {Social: <FaTwitter /> , link: 'https://twitter.com/'},
]

const FooterTwo = () => {
    return (
        <div className="footer-style-2 ptb--30 bg_image bg_image--1" data-black-overlay="6">
            <div className="wrapper plr--50 plr_sm--20">
                <div className="row align-items-center justify-content-between">
                    <div className="col-lg-4 col-md-6 col-sm-6 col-12">
                        <div className="inner">
                            <div className="logo text-center text-sm-left mb_sm--20">
                                {/* <a href="/home-one"> */}
                                <Link to="/home-one">
                                    <img src={logoforweb} height="100px" alt="Logo images"/>
                                </Link>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-6 col-sm-6 col-12">
                        <div className="inner text-center">
                            <ul className="social-share rn-lg-size d-flex justify-content-center liststyle">
                                {SocialShare.map((val , i) => (
                                    <li key={i}>
                                        <a href={`${val.link}`}>{val.Social}</a></li>
                                ))}
                            </ul>
                        </div>
                    </div>
                   
                </div>
            </div>
        </div>
    )
}
export default FooterTwo;